import Table from "react-bootstrap/Table";
import "bootstrap/dist/css/bootstrap.min.css";
import { useEffect, useState } from "react";
const ProdTable = ({ uretim, page, sevk, talep }) => {
  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Stok Kodu</th>
          <th>rn</th>
          <th>Talep (tepsi)</th>
          <th>retim</th>
          <th>Sevk</th>
        </tr>
      </thead>
      <tbody>
        {uretim[page] &&
          uretim[page].map((urun, index) => {
            return (
              <tr key={index}>
                <td>{urun.stokKodu}</td>
                <td>{urun.name}</td>
                <td>
                  {talep[0] &&
                  talep[0][page] &&
                  talep[0][page][
                    talep[0][page].findIndex(
                      (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                    )
                  ] &&
                  talep[0][page][
                    talep[0][page].findIndex(
                      (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                    )
                  ].talepMiktari > 0
                    ? talep[0][page][
                        talep[0][page].findIndex(
                          (talepUrun) => talepUrun.stokKodu === urun.stokKodu
                        )
                      ].talepMiktari
                    : 0}
                </td>
                <td>
                  {parseFloat(urun.receteyeGoreKg).toFixed(2)} KG (
                  {urun.uretimMiktari} tepsi)
                </td>
                <td>
                  {sevk[page] &&
                    sevk[page][
                      sevk[page].findIndex(
                        (sevkurun) => sevkurun.stokKodu === urun.stokKodu
                      )
                    ] &&
                    parseFloat(
                      sevk[page][
                        sevk[page].findIndex(
                          (sevkurun) => sevkurun.stokKodu === urun.stokKodu
                        )
                      ].sevkMiktari
                    ).toFixed(2) + " KG"}
                </td>
              </tr>
            );
          })}
      </tbody>
    </Table>
  );
};

export default ProdTable;
